<template>
  <div class="product-list" v-loading="loading">
    <div class="search-panel">
      <div class="cell">
        <div class="input-title">产品名称</div>
        <el-input class="input" v-model="productName" placeholder="输入产品名称"></el-input>
      </div>
      <div class="cell">
        <div class="input-title">添加时间</div>
        <el-date-picker
          v-model="datePicker"
          type="daterange"
          range-separator="至"
          format="yyyy-MM-dd HH:mm:ss"
          start-placeholder="开始日期"
          end-placeholder="结束日期">
        </el-date-picker>
      </div>
      <div class="search" @click="loadData">查询</div>
      <div class="empty"></div>
      <div class="add-goods" @click="addGoods">添加商品</div>
      <div class="integral">剩余积分：{{integral}}</div>
    </div>
    <div class="filter">
      <div class="condition" @click="changePublishStatus(-1)" :class="{ 'condition-selected': publishStatus === -1 }">全部</div>
      <div class="condition" @click="changePublishStatus(1)" :class="{ 'condition-selected': publishStatus === 1 }">已上架</div>
      <div class="condition" @click="changePublishStatus(0)" :class="{ 'condition-selected': publishStatus === 0 }">已下架</div>
    </div>
    <div class="goods-panel" style="margin-bottom: 9px;">
      <div class="main">商品信息</div>
      <div class="price">价格</div>
      <div class="commission-rate">佣金率</div>
      <div class="operation">操作</div>
    </div>
    <div class="goods-panel" v-for="(item, index) in dataArray" :key="index">
      <div class="main">
        <img :src="item.albumPics" alt="">
        <div class="info">
          <div class="name">{{item.name}}</div>
          <div class="goods-id">商品ID：{{item.id}}</div>
          <div class="shop">店铺：{{item.shopName}}</div>
        </div>
      </div>
      <div class="price">￥159</div>
      <div class="commission-rate">17%</div>
      <div class="operation">
        <div class="but put" v-if="item.publishStatus === 0" @click="putOnPublish(item)">上架</div>
        <div class="but off" v-else @click="putOffPublish(item)">下架</div>
        <div class="but edit">编辑</div>
        <div class="but delete" @click="handleDelete(item)">删除</div>
      </div>
    </div>
    <el-pagination
      background
      layout="->, prev, pager, next"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="pageSize"
      :total="total">
    </el-pagination>
  </div>
</template>

<script>
import { fetchProductList, updatePublishStatus, updateDeleteStatus, fetchUserInfo } from '@/api/tkpro'
export default {
  name: 'product-list',
  data() {
    return {
      publishStatus: -1,
      loading: false,
      integral: 0,
      datePicker: [],
      productName: '',
      currentPage: 1,
      pageSize: 10,
      total: 0,
      dataArray: [],
    }
  },
  created(){
    this.loadData()
    this.loadIntegral()
  },
  methods:{
    loadData () {
      this.loading = true
      let startTime = this.datePicker&&this.datePicker.length > 1 ? new Date(this.datePicker[0]).format() : ''
      let endTime = this.datePicker&&this.datePicker.length > 1 ? new Date(this.datePicker[1]).format() : ''
      let publishStatus = this.publishStatus === -1 ? '' : this.publishStatus
      fetchProductList(this.productName, startTime, endTime, publishStatus, this.currentPage).then(res => {
        this.dataArray = res.data.list
        this.total = res.data.total
        // this.pageSize = res.data.pageSize
        this.loading = false
      }).catch(error => {
        console.log(error);
        this.loading = false
      })
    },
    loadIntegral () {
      fetchUserInfo('stormfa').then(res => {
        if (res.code !== 200) return this.$message.error(res.message);
        this.integral = res.data.rewardPoint
        console.log(this.integral);
      }).catch(error => {
        console.log(error);
      })
    },
    changePublishStatus (index) {
      if (this.publishStatus === index) return
      this.publishStatus = index
      this.loadData()
    },
    // 上架
    putOnPublish (data) {
      const input = {
        "ids": [data.id],
        "publishStatus": 1
      }
      updatePublishStatus(input).then(res => {
        if (res.code !== 200) return this.$message.error(res.message);
        this.$message({ type: 'success', message: `操作成功` });
        this.loadData()
      }).catch(error => {
        console.log(error);
      })
    },
    // 下架
    putOffPublish (data) {
      const input = {
        "ids": [data.id],
        "publishStatus": 0
      }
      updatePublishStatus(input).then(res => {
        if (res.code !== 200) return this.$message.error(res.message);
        this.$message({ type: 'success', message: `操作成功` });
        this.loadData()
      }).catch(error => {
        console.log(error);
      })
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.loadData()
      console.log(`当前页: ${val}`);
    },
    addGoods () {
      this.$router.push({path:'/pms/addProduct'});
    },
    handleDelete (data) {
      this.$alert('您确认删除吗？', '提示', {
        confirmButtonText: '确定',
        callback: action => {
          console.log(action);
          if (action === 'cancel') return
          const input = { "ids": [data.id] }
          console.log(input);
          updateDeleteStatus(input).then(res => {
            if (res.code !== 200) return this.$message.error(res.message);
            console.log(res);
            this.$message({ type: 'success', message: `删除成功` });
            this.loadData()
          }).catch(error => {
            console.log(error);
          })
        }
      });
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.product-list {
  background: #eeeeee;
  padding: 20px;
  .search-panel {
    display: flex;
    align-items: center;
    .cell {
      display: flex;
      align-items: center;
      margin-right: 10px;
      .input-title {
        width: 70px;
        height: 20px;
        color: rgba(16, 16, 16, 100);
        font-size: 13px;
        text-align: left;
      }
      .input {
      }
    }
    .search {
      width: 85px;
      height: 32px;
      line-height: 28px;
      border-radius: 5px;
      background-color: rgba(24, 144, 255, 100);
      color: rgba(16, 16, 16, 100);
      font-size: 14px;
      text-align: center;
      border: 1px solid rgba(24, 144, 255, 100);
      color: rgba(255, 255, 255, 100);
      font-size: 14px;
      background: #1890ff;
      cursor: pointer;
    }
    .empty {
      flex: 1;
    }
    .add-goods {
      width: 107px;
      height: 32px;
      line-height: 32px;
      border-radius: 5px;
      background-color: rgba(255, 152, 0, 100);
      color: rgba(16, 16, 16, 100);
      font-size: 14px;
      text-align: center;
      color: rgba(255, 255, 255, 100);
      font-size: 14px;
      margin-right: 20px;
      cursor: pointer;
    }
    .integral {
      width: 120px;
      height: 20px;
      color: rgba(16, 16, 16, 100);
      font-size: 14px;
    }
  }
  .filter {
    display: flex;
    align-items: center;
    margin: 11px 0;
    .condition {
      width: 42px;
      height: 20px;
      color: #101010;
      font-size: 14px;
      text-align: left;
      margin-right: 20px;
      cursor: pointer;
    }
    .condition-selected {
      color: #031FBB;
      font-weight: bold;
    }
  }
  .goods-panel {
    padding: 10px;
    display: flex;
    align-items: center;
    background-color: rgba(255, 255, 255, 100);
    border: 1px solid rgba(187, 187, 187, 100);
    margin-bottom: 24px;
    .main {
      width: 36%;
      color: rgba(16, 16, 16, 100);
      font-size: 14px;
      display: flex;
      align-items: center;
      img {
        width: 70px;
        height: 71px;
        margin-right: 12px;
      }
      .info {
        color: rgba(16, 16, 16, 100);
        font-size: 14px;
        .name {
          margin-bottom: 10px;
        }
        .goods-id {
          margin-bottom: 10px;
        }
        .shop {

        }
      }
    }
    .price {
      width: 9%;
      color: rgba(16, 16, 16, 100);
      font-size: 14px;
    }
    .commission-rate {
      width: 25%;
      color: rgba(16, 16, 16, 100);
      font-size: 14px;
    }
    .operation {
      width: 28%;
      color: rgba(16, 16, 16, 100);
      font-size: 14px;
      display: flex;
      align-items: center;
      .but {
        border-radius: 4px;
        color: #fff;
        font-size: 14px;
        width: 80px;
        height: 30px;
        line-height: 28px;
        text-align: center;
        margin-right: 20px;
        cursor: pointer;
      }
      .put {
        background-color: rgba(111, 178, 223, 100);
      }
      .off {
        background-color: rgba(255, 0, 0, 100);
      }
      .edit {
        background-color: rgba(164, 173, 179, 100);
      }
      .delete {
        border: 1px solid rgba(187, 187, 187, 100);
        color: rgba(16, 16, 16, 100);
      }
    }
  }
}
</style>



